* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
  border: none;
  color: var(--text-color-primary);
}
:root {
  --button-padding-x: 20px;
  --primary-color: #f8ff9d;
  --secondary-color: #84ce62;
  --tertiary-color: #71b352;
  --danger-color: #e76645;
  --text-color-primary: #f3defd;
  --background-color: #2a1a3a;
  --font-family: Raleway, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

html {
  background-color: var(--background-color);
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 100vw;
  min-height: 100vh;
}

.App {
  font-family: var(--font-family);
  text-align: center;
  padding: 10px;
}
.App * {
  margin: 10px;
}

.curseInput {
  width: min(500px, 80%);
  padding: 20px;
}

.protectionButton {
  padding-left: var(--button-padding-x);
  padding-right: var(--button-padding-x);
  height: 30px;
  color: var(--background-color);
  font-weight: bold;
  font-family: var(--font-family);
  font-size: 14px;
  letter-spacing: 1px;
  border-radius: 2.5px;
  transition: all 0.1s ease-in-out;
}

.enabled {
  background: linear-gradient(
    to right bottom,
    var(--primary-color),
    var(--danger-color)
  );
}
.disabled {
  background: linear-gradient(
    to right bottom,
    var(--primary-color),
    var(--secondary-color)
  );
}
.protectionButton:hover {
  cursor: pointer;
  transform: translateY(-1px) scale(103%, 103%);
}
.protectionButton:active {
  box-shadow: none;
  transform: translateY(1px) scale(99%, 99%);
}
.curseHeading {
  background: linear-gradient(to right bottom, var(--secondary-color), #fff);
  -webkit-background-clip: text;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-text-fill-color: transparent;
}
.curseInput {
  color: #2a1a3a;
  font-weight: bold;
  font-size: 18px;
  border-radius: 2.5px;
}
.protected {
  outline: 2px solid var(--secondary-color);
  outline-offset: 6px;
}
.unprotected {
  outline: 2px solid var(--danger-color);
  outline-offset: 4px;
}
.curseInput::placeholder {
  color: var(--background-color);
  font-weight: normal;
}

.info {
  font-size: 12px;
}
